<template>
  <!-- Myself -->
  <div class="form-wrap">
    <p class="pTitle">
      <span class="member">
        Member
        <span class="member-num">{{formdata.member}}</span>
      </span>
      <span class="member-title">{{formdata.title}}</span>
    </p>
    <!-- 用户信息输入框 -->
    <div class="form-item">
      <p>Gender*</p>
      <!--  @blur="genderBlur" -->
      <cube-button>
        <input
          @focus="showPicker"
          ref="inputGender"
          class="Gender"
          v-model="form.gender"
          type="text"
          placeholder="Gender"
        />
      </cube-button>
    </div>
    <div class="form-item">
      <p>Surname*</p>
      <!-- @blur="SurnameBlur" -->
      <input class="Gender" v-model="form.surname" type="text" placeholder="Surname" />
    </div>

    <div class="form-item">
      <p>Given Name*</p>
      <!-- @blur="givenameBlur" -->
      <input class="Gender" v-model="form.givename" type="text" placeholder="Given Name" />
    </div>
    <!-- 日期插件 -->
    <cube-button>
      <div class="form-item">
        <p>Date of birth*</p>
        <!-- @blur="birthDateBlur" -->
        <input
          ref="inputBirth"
          class="Gender"
          @focus="showDatePicker"
          v-model="form.datebirth"
          type="text"
          placeholder="Date of birth"
        />
      </div>
    </cube-button>
    <!-- 选择HKID按钮和passport Number按钮 -->
    <div>
      <button class="HKID" @click="inputHkid" :class="[select==1?'blue':'']">
        HKID
        <i :class="[select==1?'cubeic-ok':'']"></i>
      </button>
      <button class="passport" @click="inputPassport" :class="[select==2?'blue':'']">
        Passport Number
        <i :class="[select==2?'cubeic-ok':'']"></i>
      </button>
    </div>
    <!-- 输入框 -->
    <div class="HK">
      <span class="hkNumber">{{select==1?'HKID Number :':'Passport Number :'}}</span>
      <div class="right" v-show="select == 1">
        <span>
          <!--  @blur="HkidBlur" -->
          <input
            v-model="form.HKID1"
            class="IDnumber"
            ref="HKID1"
            type="text"
            placeholder="A123456"
          />
        </span>
        <span v-show="select == 1">(</span>
        <span v-show="select == 1">
          <input ref="HKID2" v-model="form.HKID2" class="num" type="text" placeholder="7" />
        </span>
        <span v-show="select == 1">)</span>
      </div>
      <div class="right" v-show="select == 2">
        <span>
          <!-- @blur="passportBlur" -->
          <input
            ref="HKID_passport"
            v-model="form.HKID_passport"
            :class="[select==2?'width':'']"
            class="IDnumber"
            type="text"
            placeholder="A123456"
          />
        </span>
      </div>
    </div>
  </div>
</template>

