    //点击编辑回到编辑页后,循环遍历对象生产一个全新的对象
    getNemObj() {
      if (this.$store.state.userInfo) {
        console.log("当userInfo有值时,把值赋值给form");
        this.form = {};
        for (var key in this.formdata) {
          this.form[key] = this.formdata[key];
        }
        //判断再次进入信息编辑页的时候passport是否有值
        if (this.form.HKID_passport != "") {
          this.select = 2;
        }
      }
    },
    //点击日期插件
    showDatePicker() {
      if (!this.datePicker) {
        this.datePicker = this.$createDatePicker({
          title: "Date of birth",
          min: new Date(1980, 7, 28),
          max: new Date(),
          value: new Date(),
          cancelTxt: "Cancel",
          confirmTxt: "Confirm",
          onSelect: this.selectHandle,
          onCancel: this.cancelHandle
        });
      }
      this.datePicker.show();
    },
    //点击确认事件
    selectHandle(date, selectedVal, selectedText) {
      this.form.datebirth = this.dateTozero(selectedVal).join("-");
      console.log(this.form.datebirth);
      //因为cube-ui再次选完日期不会修改日期的显示
      this.$refs.inputBirth.value = this.form.datebirth;
    },
    //点击取消
    cancelHandle() {},
    //数字小于10时前面加个0
    dateTozero(arr) {
      return arr.map(item => (item < 10 ? "0" + item : "" + item));
    },

    //点击选择性别插件
    showPicker() {
      const column1 = [
        { text: "male", value: "male" },
        { text: "female", value: "female" },
        { text: "男", value: "男" },
        { text: "女", value: "女" }
      ];
      if (!this.picker) {
        this.picker = this.$createPicker({
          title: "Gender",
          data: [column1],
          cancelTxt: "Cancel",
          confirmTxt: "Confirm",
          onSelect: this.selectGender,
          onCancel: this.cancelGender
        });
      }
      this.picker.show();
    },
    //选择性别点击确认事件
    selectGender(selectedVal, selectedIndex, selectedText) {
      this.form.gender = selectedText.join(" ");
      console.log(selectedText.join(" "));
      this.$refs.inputGender.value = this.form.gender;
    },
    //选择性别点击取消事件
    cancelGender() {}
  },
  computed: {}
