</script>

<style lang="less" scoped>
@import "~style/mixin.less";
.form-wrap {
  padding: 20px;
  box-shadow: 0px 5px 10px 0 rgba(0, 0, 0, 0.14);
  // line-height: 65px;
  margin-bottom: 30px;
  p {
    display: flex;
    justify-content: space-between;

    .member {
      color: @blue;
      font-weight: bold;
      font-size: 32px;
    }
    .member-num {
      color: @blue;
      font-weight: bold;
      font-size: 32px;
      margin-left: 15px;
    }
    .member-title {
      font-weight: bold;
      font-size: 32px;
      color: rgb(0, 0, 0);
      font-family: HelveticaNeue-Medium;
    }
  }
  .pTitle {
    margin-bottom: 40px;
  }
  .Gender {
    height: 75px;
    width: 100%;
    border-radius: 15px;
    font-size: 30px;
    border: 2px solid rgba(155, 155, 155, 0.5);
    text-indent: 20px;
    font-weight: bold;
  }
  input::-webkit-input-placeholder {
    color: rgb(233, 233, 233);
    letter-spacing: 2px;
    font-weight: 100;
  }
  .form-item {
    p {
      color: rgb(0, 0, 0);
    }
    line-height: 35px;
    margin-top: 20px;
  }
  .HKID {
    width: 316px;
    height: 56px;
    color: white;
    font-size: 30px;
    outline: none;
    border: none;
    background: rgb(236, 235, 235);
    border-radius: 10px;
    margin-top: 40px;
  }
  .passport {
    width: 316px;
    height: 56px;
    padding: 0;
    text-align: center;
    color: white;
    font-size: 30px;
    background: rgb(236, 235, 235);
    outline: none;
    border: none;
    border-radius: 10px;
    margin-top: 40px;
  }
  .blue {
    background: @blue;
  }
  .HK {
    margin-top: 30px;
    font-size: 32px;
    margin-bottom: 10px;
    .left {
      display: inline-block;
    }
    .right {
      display: inline-block;
    }
    .hkNumber {
      color: rgb(0, 0, 0);
      // font-weight: bold;
      font-family: HelveticaNeue;
    }
    span {
      .IDnumber {
        width: 235px;
        height: 60px;
        border-radius: 13px;
        font-size: 30px;
        padding-right: 15px;
        border: 2px solid rgba(155, 155, 155, 0.5);
        margin-left: 20px;
        text-align: right;
      }
      .num {
        width: 75px;
        height: 60px;
        border-radius: 13px;
        font-size: 30px;
        border: 2px solid rgba(155, 155, 155, 0.5);
        text-align: center;
      }
      .width {
        width: 300px;
      }
    }
  }
  .cube-btn {
    padding: 0;
    background-color: #fff;
  }
}
</style>


