import Vue from 'vue'
import Router from 'vue-router'
const Index = () => import ( /* webpackChunkName: "Index" */ 'pages/Index') // 路由推崇这种写法按需加载 中间的webpackChunkName 为分包可要可不要 官方文档有
import Login from 'pages/Login'
import TravelInfo from 'pages/TravelInfo'
import Infoconfirm from 'pages/Infoconfirm'
import Transtion from 'pages/Transtion'
import Prompt from 'pages/Prompt'
import MemberData from 'pages/MemberData'
import ConfirmOrder from 'pages/ConfirmOrder'
import Payment from 'pages/Payment'
import Complete from 'pages/Complete'
import CoverageDetails from 'pages/CoverageDetails';
import Policyholder from 'pages/Policyholder'




Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index
    },
    {
      path: '/TravelInfo',
      name: 'travelInfo',
      component: TravelInfo
    },
    {
      path: '/Infoconfirm',
      name: 'infoconfirm',
      component: Infoconfirm
    },
    {
      path: '/Login',
      name: 'login',
      component: Login
    },
    {
      path: '/Transtion',
      name: 'transtion',
      component: Transtion
    },
    {
      path: '/Prompt',
      name: 'prompt',
      component: Prompt
    },
    {
      path: '/MemberData',
      name: 'memberData',
      component: MemberData
    },
   
    {
      path: '/ConfirmOrder',
      name: 'confirmOrder',
      component: ConfirmOrder
    },
    {
      path: '/Payment',
      name: 'payment',
      component: Payment
    },
   
    {
      path: '/Complete',
      name: 'complete',
      component: Complete
    },
    {
      path: '/CoverageDetails',
      name: 'coverageDetails',
      component: CoverageDetails
    },
    {
      path: '/Policyholder',
      name: 'Policyholder',
      component: Policyholder
    }
  ]
})



