<!--
 * @Description: In User Settings Edit
 * @Author: your name
 * @Date: 2019-09-03 12:19:42
 * @LastEditTime: 2019-09-17 12:18:52
 * @LastEditors: Please set LastEditors
 -->
<template>
  <div class="page-wrap">
    <div class="header progressive">
      <!-- <span>Fintech</span><span>travel.com</span> -->
      <img class="preview" v-progressive="imgs.preview" :src="imgs.src" alt />
    </div>
    <p class="title">Welcome!</p>
    <p class="tip-text">Enter your mobile number and email to continue.</p>
    <p class="label">Enter your mobile number</p>
    <div class="flex ver-mid">
      <div class="area-wrap">
        <span class="areaCode">{{areaCode}}</span>
        <cube-select
          class="areaSelect"
          v-model="areaCode"
          placeholder=" "
          title=" "
          cancelTxt="Cancel"
          confirmTxt="Confirm"
          :options="areaOptions"
        >
          <span>1</span>
        </cube-select>
      </div>
      <cube-input class="input-item" v-model="form.mobile" placeholder="8888 8888"></cube-input>
    </div>
    <!-- 用来提示手机号输入错误信息 -->
    <p style="margin-top:10px;color:red;">{{mobileError}}</p>
    <p class="label" style="margin-top:30px">Enter your email</p>
    <cube-input class="input-item" v-model="form.email" placeholder="example@email.com"></cube-input>
    <!-- 用来提示邮箱输入错误信息 -->
    <p style="margin-top:10px;color:red;">{{emailError}}</p>

    <div class="form-tip">(ePolicy will be sent to your email address written above)</div>
    <p class="tixing">
      If you click on “Next” below, it means that you agree to our
      <span class="terms">terms</span> and
      <span class="privacy">privacy policies</span> .
    </p>
    <div class="button-bar">
      <router-link to>
        <button @click="next" class="next">Next</button>
      </router-link>
    </div>

    <!-- 底部 -->
    <div class="footer">
      <span class="Powered">Powered By</span>
      <span class="footerimgs">
        <img class="footerimg" src="../assets/images/Logo A@3x.png" alt />
      </span>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
import http from "@/api/http";
export default {
  name: "Index",
  data() {
    return {
      imgs: {
        src: require("@/assets/images/Fintechtravel.com@xxxhdpi.png"),
        preview: require("@/assets/images/Fintechtravel.com@xxxhdpi.png")
      },
      areaCode: "+86",
      areaOptions: [
        { value: "+852", text: "Hong Kong +852" },
        { value: "+63", text: "Philippine +63" },
        { value: "+62", text: "Indonesia +62" },
        { value: "+86", text: "China +86" }
      ],
      form: {
        mobile: "",
        email: ""
      },
      mobileError: "",
      emailError: "",
      //定义一个对象来保存输入的错误信息
      validateObj: {
        flag: true,
        msg: ""
      }
    };
  },
  created() {
    //过渡页(1秒之后消失)
    const toast = this.$createToast({
      time: 1000,
      txt: "loading..."
    });
    toast.show();
  },
  mounted() {
    
    
  },

  methods: {
    ...mapMutations(["setAreacode"]),
    //点击Next事件(检验输入的内容是否符合规定)
    next() {
      this.validate(); //最上面调用表单校验方法
      if (!this.validateObj.flag) {
        // 如果表单某项有错误则弹出toast 并带出错误信息this.validateObj.msg
        this.$createToast({
          txt: this.validateObj.msg,
          type: "txt"
        }).show();
        return false;
      }

      //参数
      const params = {
        phone: this.form.mobile,
        phone_country_code: this.areaCode,
        web: "www.baidu.com",
        email: this.form.email
      };
      this.$http.post("/wesupromo/web/add", params).then(res => {
        if (res.status === 100) {
          console.log(res);
          sessionStorage.setItem("userId", res.data.id);
          //保存区号
          this.setAreacode(this.areaCode);
          this.$router.push({
            path: "/Transtion",
            query: { phone_country_code: this.areaCode }
          });
        }
      });
    },
    //检验表单输入的情况,把错误信息保存到定义的对象validateObj中
    validate() {
      this.validateObj = {
        flag: true,
        msg: ""
      };
      //检验手机号
      if (this.form.mobile == "") {
        this.validateObj.flag &&
          (this.validateObj = { flag: false, msg: "手机号不能为空" });
      } else if (this.form.mobile != "") {
        const reg = /^1[3456789]\d{9}$/;
        if (this.areaCode == "+86") {
          //+86的区号,手机号需要11位数字
          !reg.test(this.form.mobile) &&
            this.validateObj.flag &&
            (this.validateObj = { flag: false, msg: "请输入有效的手机号" });
        } else if (this.areaCode == "+852") {
          //+852的区号,手机号需要8位数字
          this.form.mobile.length != 8 &&
            this.validateObj.flag &&
            (this.validateObj = { flag: false, msg: "请输入有效的手机号" });
        } else {
          //其他的区号手机号需要大于5位数字
          this.form.mobile.length <= 5 &&
            this.validateObj.flag &&
            (this.validateObj = { flag: false, msg: "请输入有效的手机号" });
        }
      }
      //检验邮箱
      if (this.form.email == "") {
        this.validateObj.flag &&
          (this.validateObj = { flag: false, msg: "邮箱不能为空" });
      } else if (this.form.email != "") {
        const reg = /^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
        !reg.test(this.form.email) &&
          this.validateObj.flag &&
          (this.validateObj = { flag: false, msg: "请输入有效的邮箱" });
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@import "~style/mixin.less";
.page-wrap {
  padding: 80px 25px 104px 54px;
  background: @background;
  .header {
    text-align: center;
    width: 500px;
    height: 250px;
    margin: 0 auto;
    img {
      width: 100%;
      height: 100%;
    }
  }
  .title {
    color: #000;
    font-weight: 600;
    font-size: 48px;
    line-height: 58px;
  }
  .tip-text {
    font-size: 32px;
    line-height: 41px;
    letter-spacing: 2px;
    color: @gray;
    margin-bottom: 102px;
    margin-top: 20px;
  }
  .label {
    font-size: 24px;
    letter-spacing: 1px;
    color: @gray;
    line-height: 28px;
    margin-bottom: 20px;
  }
  .area-wrap {
    position: relative;
    width: 160px;
    border-bottom: 4px solid #000;
    margin-right: 18px;
    .areaCode {
      position: absolute;
      height: 90px;
      right: 30px;
      left: 0;
      text-align: right;
      font-size: 44px;
      line-height: 90px;
      z-index: 1;
      color: #000;
    }
    .areaSelect {
      background: transparent;
      position: relative;
      z-index: 2;
      overflow: hidden;
      height: 90px;
      /deep/ .cube-select-text {
        color: transparent;
      }
      &::after {
        border: none;
      }
      /deep/ span {
        font-size: 44px;
        color: #000;
      }
      /deep/ .cube-select-icon {
        border-width: 10px 6px 0 6px !important;
        border-color: #000 transparent transparent;
      }
    }
  }
  .input-item {
    height: 91px;
    line-height: 70px;
    font-size: 35px;
    border-bottom: 4px solid #000;
    &::after {
      border: none;
    }
    /deep/ input {
      color: #000;
      padding: 0;
      height: 80px;
      padding-left: 20px;
      margin: 0;
    }
    /deep/ input::-webkit-input-placeholder {
      color: rgb(233, 233, 233);
      letter-spacing: 2px;
    }
  }
  .form-tip {
    margin-top: 6px;
    font-size: 20px;
    color: @gray;
    line-height: 24px;
  }
  .tixing {
    font-size: 22px;
    line-height: 24px;
    margin-top: 120px;
  }
  .terms {
    color: #0081ff;
    text-decoration: underline;
  }
  .privacy {
    color: #0081ff;
    text-decoration: underline;
  }
  .button-bar {
    margin: 0 auto;
    text-align: center;
    margin-top: 54px;
    margin-bottom: 102px;
  }
  .next {
    width: 388px;
    height: 130px;
    line-height: 130px;
    border: none;
    border-radius: 10px;
    background: rgb(2, 164, 71);
    color: #fff;
    text-align: center;
    font-size: 36px;
  }
  .footer {
    text-align: center;
    vertical-align: middle;
    .Powered {
      display: inline-block;
      font-size: 25px;
      font-weight: bold;
      margin-top: 100px;
      margin-left: -75px;
      color: rgb(88, 89, 94);
    }
    .footerimgs {
      display: inline-block;
      vertical-align: middle;
      width: 312px;
      height: 94px;
      line-height: 50px;
    }
    .footerimg {
      display: inline-block;
      vertical-align: middle;
      width: 100%;
      height: 100%;
    }
  }
}
</style>



