<!--
 * @Description: In User Settings Edit
 * @Author: your name
 * @Date: 2019-09-04 10:35:48
 * @LastEditTime: 2019-09-17 12:22:42
 * @LastEditors: Please set LastEditors
 -->
<template>
  <div class="page-wrap">
    <p class="title">{{data.displayed_owned_by}}</p>

    <div class="headerimgs">
      <img class="headerimg" :src="data.progress_logo" alt />
    </div>

    <p class="travelead">{{data.name}}</p>

    <p class="budget">{{data.desc1}}</p>

    <div class="travel-insurance">{{data.category_name}}</div>

    <!-- 价格 -->
    <div class="price">HK${{price}}</div>

    <div>
      <img class="box" :src="data.products_imgs[0]" alt />
    </div>

    <!-- 总天数和总人数 -->
    <div class="people">
      <span class="days">
        <span>{{days}}</span>Days
      </span>
      <span class="line">|</span>
      <span class="adults">
        <img class="adultsimg" src="@/assets/images/Adults@3x.png" alt />
      </span>
      <span class="adultsnum">{{adults}}</span>
      <span class="friends">
        <img class="friendsimg" src="@/assets/images/friends Copy@3x.png" />
      </span>
      <span class="friendsnum">{{childs}}</span>
      <span>
        <i class="cubeic-arrow"></i>
      </span>
    </div>

    <!-- Key Features -->
    <div>
      <p class="key">Key Features</p>
      <div class="key-content">
        <div class="features-wrap">
          <p :class="{'features': flag}">{{data.remarks}}</p>
          <span class="more" @click="clickmore">{{flag ? "more" : "hide"}}</span>
        </div>
      </div>
    </div>

    <!-- Customer Service -->
    <div class="customers" @click="customers">
      <span class="starrs">
        <img class="starr" :src="data.cs_logo" alt />
      </span>
      <span class="customer">Customer Service</span>
      <i class="cubeic-arrow"></i>
    </div>

    <!-- Coverage Details -->
    <div class="coverages" @click="coverage">
      <span class="covers">
        <img class="cover" src="@/assets/images/Confirmation of cover@3x.png" alt />
      </span>
      <span class="coverage">Coverage Details</span>
      <i class="cubeic-arrow"></i>
    </div>

    <div class="button-bar">
      <button class="proceed" @click="proceed">Proceed</button>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  data() {
    return {
      more: false,
      data: "",
      flag: true,
      days: "",
      price: ""
    };
  },
  computed: {
    ...mapState(["travelInfo", "product_id", "language", "plan"]),
    //计算成人和小孩的人数
    adults() {
      return (
        this.travelInfo.count.myself +
        this.travelInfo.count.spouse +
        this.travelInfo.count.friends
      );
    },
    childs() {
      return this.travelInfo.count.child;
    }
  },
  methods: {
    //点击更多显示更多
    clickmore() {
      this.flag = !this.flag;
    },
    //点击Customer Service事件
    customers() {
      this.$createDialog({
        type: "alert",
        confirmBtn: { text: "確認", active: true, href: "javascript:;" },
        content: this.data.customer_service
        // '如有查询，请联络Starr:<a href="mailto:(852)37655566/cs.hk@starrcompanies.com">(852)37655566/cs.hk@starrcompanies.com</a>(办公时间：星期一至星期五，上午9:00至12:30；下午1:30至下午5:00)'
      }).show();
    },
    //点击Coverage Details事件
    coverage() {
      this.$router.push({
        path: "/CoverageDetails",
        query: { product_id: this.product_id }
      });
    },
    //点击Proceed事件
    proceed() {
      this.$router.push("/Login");
    }
  },
  created() {},
  mounted() {
    //区号
    var areacode = this.travelInfo.phone_country_code;
    //天数
    this.days = this.travelInfo.days;
    var params = {
      language: this.language,
      product_id: this.product_id,
      phone_country_code: areacode
    };
    //获取产品详情
    this.$http
      .post("/wesupromo/web/tpc/getProductDetails", params)
      .then(res => {
        this.data = res.data;
        console.log(res.data);
        var strdata = this.data.remarks;
      });

    // 报价
    let others_insured_list = [];
    const { count } = this.travelInfo;
    if (count.myself) {
      others_insured_list.push({ relation: "2001" });
    }
    if (count.spouse) {
      others_insured_list.push({ relation: "2002" });
    }
    if (count.child) {
      others_insured_list.push({ relation: "2003" });
    }
    if (count.friends) {
      others_insured_list.push({ relation: "2004" });
    }

    var priceParams = {
      product_id: this.plan.product_id,
      language: "03dfd96d875d43ba8ced522f3bbc0179",
      start_date: this.travelInfo.date.departureDate,
      end_date: this.travelInfo.date.returnDate,
      others_insured_list
    };
    // 日计划  月计划   年计划
    console.log(this.travelInfo.date.departureDate);
    console.log(this.travelInfo.date.returnDate);

    //调用接口获取价格
    this.$http
      .post("/wesupromo/web/product/getstarrquote", priceParams)
      .then(res => {
        console.log(res);
        this.price = res.data.premium;
      });
  }
};
</script>

<style lang="less" scoped>
@import "~style/mixin.less";
.page-wrap {
  padding: 34px;
  padding-bottom: 260px;
  background-color: #fff;
  position: relative;
  .title {
    color: rgb(155, 155, 155);
    font-size: 9px;
  }
  .travelead {
    color: #000;
    font-weight: 700;
    font-size: 44px;
    line-height: 52px;
    margin-bottom: 15px;
  }
  .budget {
    color: #000;
    font-weight: 600;
    font-size: 34px;
    line-height: 42px;
    margin-bottom: 10px;
  }
  .travel-insurance {
    color: rgb(142, 142, 147);
    font-size: 30px;
  }
  .headerimgs {
    width: 165px;
    height: 136px;
  }
  .headerimg {
    width: 100%;
    height: 100%;
  }
  .price {
    text-align: right;
    color: rgb(12, 129, 255);
    font-size: 35px;
    font-weight: bold;
    margin-bottom: 35px;
  }
  .box {
    border-radius: 22px;
    width: 100%;
  }
  .people {
    // width: 100%;
    border-bottom: 1px solid rgb(209, 208, 208);

    line-height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    > span {
      vertical-align: middle;
    }
  }
  .days {
    font-size: 32px;
    color: rgb(74, 74, 74);

    > span {
      margin-left: 20px;
      margin-right: 15px;
    }
  }
  .line {
    font-size: 40px;
    color: rgb(151, 151, 151);
    margin-left: -100px;
  }
  .adults {
    display: inline-block;
    width: 47px;
    height: 47px;
    margin-right: -100px;
  }
  .adultsimg {
    width: 100%;
    height: 100%;
  }
  .friends {
    display: inline-block;
    width: 47px;
    height: 47px;
    margin-right: -100px;
  }
  .friendsimg {
    width: 100%;
    height: 100%;
  }
  .adultsnum {
    color: rgb(74, 74, 74);
    font-size: 30px;
    margin-right: -75px;
  }
  .friendsnum {
    color: rgb(74, 74, 74);
    font-size: 30px;
  }
  .cubeic-arrow {
    color: rgba(0, 0, 0, 0.25);
    font-size: 32px;
    font-weight: bold;
  }

  .trip {
    color: rgb(12, 129, 255);
    font-weight: 500;
    border-bottom: 1px solid rgb(209, 208, 208);
    line-height: 65px;
  }
  .key {
    font-size: 34px;
    font-weight: 550;
    line-height: 100px;
  }
  .key-content {
    border-bottom: 1px solid rgb(209, 208, 208);
    padding-bottom: 15px;
    p {
      font-size: 30px;
      font-family: Arial, Helvetica, sans-serif;
      line-height: 45px;
    }
  }
  .features-wrap {
    white-space: pre-line;
    position: relative;
    .more {
      display: block;
      width: 100%;
      text-align: right;
      color: rgb(12, 129, 255);
    }
  }
  .features {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    /* autoprefixer:off */
    -webkit-box-orient: vertical;
    /* autoprefixer:on */
    -webkit-line-clamp: 4;
  }

  .customers {
    line-height: 75px;
    border-bottom: 1px solid rgb(209, 208, 208);
    padding-bottom: 15px;
    display: flex;
    justify-content: space-between;
    .starrs {
      display: inline-block;
      width: 65px;
      height: 65px;
      .starr {
        width: 100%;
        height: 100%;
        vertical-align: middle;
        margin-left: 20px;
      }
    }
    .customer {
      font-size: 35px;
      font-family: Arial, Helvetica, sans-serif;
      margin-left: -220px;
    }
  }
  .coverages {
    line-height: 75px;
    padding-bottom: 15px;
    display: flex;
    justify-content: space-between;
    .covers {
      display: inline-block;
      width: 65px;
      height: 65px;
      .cover {
        width: 100%;
        height: 100%;
        vertical-align: middle;
        margin-left: 20px;
      }
    }
    .coverage {
      font-size: 35px;
      font-family: Arial, Helvetica, sans-serif;
      margin-left: -220px;
    }
  }
  .button-bar {
    text-align: center;
    position: absolute;
    bottom: 0;
    left: 50%;
    width: 100%;
    height: 240px;
    transform: translateX(-50%);
    background-color: rgba(250, 250, 250, 0.9);
    border-top: 3px solid rgba(0, 0, 0, 0.25);
  }

  .proceed {
    width: 388px;
    height: 130px;
    line-height: 130px;
    border: none;
    border-radius: 10px;
    margin-top: 50px;
    background: rgb(2, 164, 71);
    color: #fff;
    text-align: center;
    font-size: 36px;
  }
}
</style>


