<!--
 * @Description: In User Settings Edit
 * @Author: your name
 * @Date: 2019-09-04 10:33:52
 * @LastEditTime: 2019-09-17 13:05:06
 * @LastEditors: Please set LastEditors
 -->
<template>
  <div class="page-wrap">
   
    <div class="head-imgs">
      <img class="head-img" :src="this.data.logo_path" alt />
    </div>

    <div class="Starr">
      <div :class="{'desc':flag}">{{this.data.desc1}}</div>
      <span class="more" @click="clickmore">{{flag ? "more" : "hide"}}</span>
    </div>

    <div class="individual" v-html="this.data.descHtml"></div>

    <div class="button-bar">
      <router-link to="/MemberData">
        <button class="proceed">Enter</button>
      </router-link>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";

export default {
  data() {
    return {
      more: false,
      data: "",
      flag: true
    };
  },
  created() {
    const toast = this.$createToast({
      time: 1000,
      txt: "loading..."
    });
    toast.show();
  },
  methods: {
    clickmore() {
      this.flag = !this.flag;
    }
  },
  computed: {
    ...mapState(["language", "product_id"])
  },
  mounted() {
    var params = {
      product_id: this.product_id,
      language: this.language
    };
    this.$http.post("/wesupromo/web/tpc/getImportantNotes", params).then(res => {
      this.data = res.data;
      console.log(this.data);
    });
  }
};
</script>

<style lang="less" scoped>
@import "~style/mixin.less";
.page-wrap {
  padding: 34px;
  padding-bottom: 260px;
  position: relative;
   background: @background;
   
  .head-imgs {
    width: 400px;
    height: 200px;
    margin: 0 auto;
    margin-top: 20px;
    .head-img {
      width: 100%;
      height: 100%;
    }
  }
  .Starr {
    margin-top: 40px;
    border-bottom: 1px solid rgb(209, 208, 208);
    padding-bottom: 35px;
    white-space: pre-line;
    position: relative;
    font-size: 28px;
    line-height: 30px;
    color: rgb(74, 74, 74);
    
  }
  .desc {
    font-size: 28px;
    color: rgb(74, 74, 74);
    line-height: 30px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    /* autoprefixer:off */
    -webkit-box-orient: vertical;
    /* autoprefixer:on */
    -webkit-line-clamp: 6;
  }
  .more {
    color: @blue;
    position: absolute;
    right: 0;
    bottom: 0;
  }
  .individual {
    margin-top: 30px;
    padding-left: 18px;
    padding-bottom: 30px;
    font-size: 28px;
    color: rgb(74, 74, 74);
    line-height: 32px;
    margin-bottom: 5px;
  }

  .button-bar {
    margin: 0 auto;
    text-align: center;
    position: absolute;
    width: 100%;
    height: 240px;
    left: 50%;
    transform: translateX(-50%);
    border-top: 3px solid rgba(0, 0, 0, 0.25);
    background-color: rgba(250, 250, 250, 0.9);
    
  }
  .proceed {
    width: 388px;
    height: 130px;
    line-height: 130px;
    border: none;
    margin-top: 50px;
    border-radius: 10px;
    background: rgb(2, 164, 71);
    color: #fff;
    text-align: center;
    font-size: 36px;
  }
}
</style>


