 -->
<template>
  <div class="page-wrap">
    <WFormItem :ref="'input'+index" v-for="(item, index) in formData" :key="index" :formdata="item"></WFormItem>

    <div class="button-bar">
      <button @click="confirm" class="proceed">Confirm</button>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
import WFormItem from "components/WFormItem.vue";

export default {
  components: {
    WFormItem
  },
  data() {
    return {
      //保存用户输入的信息
      formData: [],
      //保存人数
      people: ""
    };
  },

  computed: {
    ...mapState(["travelInfo"]),
    ...mapState(["userInfo"])
  },

  created() {
    const toast = this.$createToast({
      time: 1000,
      txt: "loading..."
    });
    toast.show();
    this.getFormData();
  },
  methods: {
    ...mapMutations(["setUserInfo"]),
    //1.获取到信息保存到vuex中
   async confirm() {
      console.log(this.$refs.input0);
      //循环遍历获取表单数据
      const { count } = this.travelInfo;
      let num = 0;
      //接收表单输入的所有用户信息
      let getUserInfo = [];
      for (let key in count) {
        if (count[key] > 0) {
          //在循环遍历count[key]里面的数量
          for (var i = 0; i < count[key]; i++) {
            var inputNum = "input" + num;
            console.log(inputNum);
            //关键:获取到的信息
            console.log(this.$refs[inputNum][0].form);
            //把获取到的数据先保存到数组中
            getUserInfo.push(this.$refs[inputNum][0].form);
            num++;
          }
        }
      }
      console.log(getUserInfo);
      
      //验证信息是否都填写完毕
      for (var i = 0; i < getUserInfo.length; i++) {
        
        if (
          getUserInfo[i].datebirth == "" ||
          getUserInfo[i].givename == "" ||
          getUserInfo[i].gender == "" ||
          getUserInfo[i].surname == "" ||
          getUserInfo[i].HKID2 == ""
        ) {
          this.$createToast({
            txt: "有信息未填写",
            time: 3000,
            type: "txt"
          }).show();
          return false;
        }
        if (getUserInfo[i].HKID1 == "" && getUserInfo[i].HKID_passport == "") {
          this.$createToast({
            txt: "有信息未填写",
            time: 3000,
            type: "txt"
          }).show();
          return false;
        }
        //调用验证方法,验证信息填写是否符合规定
        var isverify1 = await this.verifyUserFrom(getUserInfo[i]);
        if (isverify1 == false) {
          return false;
        }
      }
      //如果验证都通过
      console.log(isverify1);
      if (isverify1 == true) {
        //把数据存到vuex中
        console.log(getUserInfo);
        this.setUserInfo(getUserInfo);
        console.log(this.travelInfo.count.myself);
        if (this.travelInfo.count.myself == 0) {
          this.$router.push({ path: "/Policyholder" });
        } else {
          this.$router.push({ path: "/confirmOrder" });
        }
      }
    },
    //2.第一次到信息页(获取输入的信息保存到vuex);第二次到信息页(获取vuex中的信息再提交后保存到vuex中)
    getFormData() {
      let num = 1;
      let index = 0;
      const titleEnum = {
        child: "My Child",
        friends: "Family and friends",
        myself: "Myself",
        spouse: "My Spouse"
      };
      const { count } = this.travelInfo;
      console.log(count);
      for (let key in count) {
        if (count[key] > 0) {
          //在循环遍历count[key]里面的数量
          for (var i = 0; i < count[key]; i++) {
            //判断userInfo有没有值
            //当用户点击exit返回到编辑也得时候,将vuex中把编辑后的数据取出来
            if (this.userInfo) {
              var gender = this.userInfo[index].gender;
              var surname = this.userInfo[index].surname;
              var givename = this.userInfo[index].givename;
              var datebirth = this.userInfo[index].datebirth;
              var HKID1 = this.userInfo[index].HKID1;
              var HKID2 = this.userInfo[index].HKID2;
              var HKID_passport = this.userInfo[index].HKID_passport;
            }
            index++;
            this.people++;
            //通过formData来渲染页面
            this.formData.push({
              title: titleEnum[key],
              member: num++,
              //当用户点击exit返回到编辑页的时候,将vuex中把编辑后的数据取出来
              //以下可用赋值简写
              gender: gender || "",
              surname: surname || "",
              givename: givename || "",
              datebirth: datebirth || "",
              HKID1: HKID1 || "",
              HKID2: HKID2 || "",
              HKID_passport: HKID_passport || ""
            });
          }
        }
      }
      console.log(this.formData);
      
    },
    //3.封装判断用户输入的信息是否符合规定
   async verifyUserFrom(getUserInfo) {
      var isverify = false;
      //验证用户性别
      
      //验证用户surname
      var surnameinput = new RegExp("^[a-zA-Z]+$");
      if (!surnameinput.test(getUserInfo.surname)) {
        this.$createToast({
          txt: "Surname输入有误,只能输入全英文且不能为空",
          time: 3500,
          type: "txt"
        }).show();
        return false;
      }
      //验证用户given name
      var surnameinput1 = new RegExp("^[a-zA-Z]+$");
      if (!surnameinput1.test(getUserInfo.givename)) {
        this.$createToast({
          txt: "Surname输入有误,只能输入全英文且不能为空",
          time: 3500,
          type: "txt"
        }).show();
        return false;
      }
      //验证passport
      var passportInput = new RegExp("^[0-9]*$");
      if (
        getUserInfo.HKID_passport.trim() != "" &&
        !passportInput.test(getUserInfo.HKID_passport)
      ) {
        this.$createToast({
          txt: "passportInput输入有误请重新输入",
          time: 3500,
          type: "txt"
        }).show();
        return false;
      }
      // 验证HKID(调用接口)
      // var HKIDInput = /^[a-z0-9]{8}$/;
      // if (getUserInfo.HKID1.trim() != ""&&!HKIDInput.test(getUserInfo.HKID1)) {
      //   this.$createToast({
      //     txt: "身份证输入有误请重新输入",
      //     time: 3500,
      //     type: "txt"
      //   }).show();
      //   return false;
      // }
      let HKID = getUserInfo.HKID1+getUserInfo.HKID2;
      //P1032651
      console.log(HKID);
       try {
        const res = await this.$http.post("/wesupromo/web/validate/validateHKIDWithLng", {
          language: "tc",
          hkid: HKID
        });
        console.log(res);
        console.log(res.status);
        if (res.status == 100) {
          console.log(res.msg);
          const toast = this.$createToast({
            time: 3500,
            type: "txt",
            txt: "身份证正确"
          });
          toast.show();
        }
      } catch (err) {
        console.log(err);
        if (err.data.is_valid == false) {
          const toast = this.$createToast({
            time: 3500,
            type: "txt",
            txt: err.msg
          });
          toast.show();
          return false;
        }
      }
      isverify = true;
      console.log(isverify);
      return isverify;
    }
  }
};
</script>

<style lang="less" scoped>
@import "~style/mixin.less";
.page-wrap {
  padding: 34px;
  background: @background;
  padding-bottom: 260px;
  position: relative;
  .imgs {
    width: 100%;
  }
  .img {
    width: 100%;
    height: 100%;
  }

  .button-bar {
    margin: 0 auto;
    text-align: center;
    position: absolute;
    width: 100%;
    height: 240px;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    border-top: 3px solid rgba(0, 0, 0, 0.25);
    background-color: rgba(250, 250, 250, 0.9);
  }
  .proceed {
    width: 388px;
    height: 130px;
    line-height: 130px;
    margin-top:50px;
    border: none;
    border-radius: 10px;
    background: rgb(2, 164, 71);
    color: #fff;
    text-align: center;
    font-size: 36px;
  }
}
</style>


