<!--
 * @Description: In User Settings Edi
 * @Author: your name
 * @Date: 2019-09-04 11:47:08
 * @LastEditTime: 2019-09-17 12:15:42
 * @LastEditors: Please set LastEditors
 -->
<template>
  <div class="page-wrap">
    <div class="header progressive">
      <img class="banner" :src="productsImg" alt />
    </div>
    <div class="container-wrap">
      <p class="title">{{$t('')}}Travel Insurance</p>
      <p class="part-title">
        <span>Departure and Return Date</span>
        <span>{{days}} days</span>
      </p>
      <div class="date-part">
        <!-- cube-ui日期插件 -->
        <cube-button @click="showDatePicker('departureDate')">
          <div class="date-pick">
            <p class="date-label">DEPARTURE DATE</p>
            <span
              :class="{'date-value':true, 'empty': !date.departureDate}"
            >{{date.departureDate ? date.departureDate : 'XXXX-XX-XX'}}</span>
          </div>
        </cube-button>
        <!-- cube-ui日期插件 -->
        <cube-button @click="showDatePicker('returnDate')">
          <div class="date-pick">
            <p class="date-label">RETURN DATE</p>
            <span
              :class="{'date-value':true, 'empty': !date.returnDate}"
            >{{date.returnDate ? date.returnDate : 'XXXX-XX-XX'}}</span>
          </div>
        </cube-button>
      </div>
      <p class="part-title">
        <span>Companion(s)</span>
        <span>{{counts}} members</span>
      </p>
      <div class="Companion-part">
        <div @click="handlecount('select','myself')">
          <span>Myself</span>
          <span :class="{'checkBox':true, 'selected':count.myself !== 0}"></span>
        </div>
        <div @click="handlecount('select','spouse')">
          <span>My Spouse</span>
          <span :class="{'checkBox':true, 'selected':count.spouse !== 0}"></span>
        </div>
        <div>
          <span>
            My Child
            <i>aged below 18</i>
          </span>
          <span>
            <i @click="handlecount('sub','child')" class="action-button sub"></i>
            <span>{{count.child}}</span>
            <i @click="handlecount('add','child')" class="action-button add"></i>
          </span>
        </div>
        <div>
          <span>Family and friends</span>
          <span>
            <i @click="handlecount('sub','friends')" class="action-button sub"></i>
            <span>{{count.friends}}</span>
            <i @click="handlecount('add','friends')" class="action-button add"></i>
          </span>
        </div>
      </div>
      <div class="button-bar">
        <button class="next" @click="showResults">Show My Results</button>
      </div>
    </div>
    <!-- 提示背景 -->
    <div v-show="isShow" class="popover_back"></div>
    <div v-show="isShow" class="popover">
      <div class="Oops">
        <img src="../assets/images/Opps@3x.png" alt />
      </div>
      <p class="title">Oops!</p>
      <div
        class="hint"
      >Child under 18 is not eligible to be the Policyholder! Please add a Policyholder to proceed your purchase! The Policyholder does not need to be one of the companions!</div>
      <div class="btn">
        <button @click="Back" class="back">Back</button>
        <button @click="Comfirm" class="comfirm">Comfirm</button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  data() {
    return {
      date: {
        departureDate: new Date().toISOString().slice(0, 10),
        returnDate: "",
        departureDateObj: new Date(),
        returnDateObj: new Date()
      },
      count: {
        myself: 0,
        spouse: 0,
        child: 0,
        friends: 0
      },

      dateType: "",
      isShow: false,
      //productsImg
      productsImg: ""
    };
  },
  mounted() {
    var params = {
      language: this.language,
      product_id: this.product_id,
      phone_country_code: this.areacode
    };
    const loading = this.$createToast({
      time: 0,
      txt: "Loading..."
    });
    loading.show();
    this.$http
      .post("/wesupromo/web/tpc/getProductDetails", params)
      .then(res => {
        if (res.status == 100) {
          this.productsImg = res.data.products_imgs;
          this.$nextTick(() => {
            loading.hide();
          });
        }
      });
  },
  created() {
    console.log(this.$store.state.travelInfo.date.returnDate);
    console.log(this.$store.state.travelInfo.count);
    //如果是选择年计划,period_type为2
    if (this.plan && this.plan.period_type == 2) {
      //转换成YYYY-MM-DD格式
      let dateArr = new Date()
        .toISOString()
        .slice(0, 10)
        .split("-");
      this.date.returnDate = [
        (Number(dateArr[0]) + 1).toString(),
        dateArr[1],
        dateArr[2] - 1
      ].join("-");
    }
    //进入到此页,判断vuex中是否有值
    if (this.travelInfo && this.travelInfo.count) {
      this.date = this.travelInfo.date;
      this.count = this.travelInfo.count;
      //禁止浏览器回退
      history.pushState(null, null, document.URL);
      window.addEventListener("popstate", function() {
        history.pushState(null, null, document.URL);
      });
      //清空vuex中的userInfo
      const resetFile = ["userInfo", "protectorInfo", "travelInfo"];
      this.reset(resetFile);
    }
    //过渡页
    const toast = this.$createToast({
      time: 1000,
      txt: "loading..."
    });
    toast.show();
  },
  methods: {
    ...mapMutations(["setTravel", "reset"]),
    //选择人数事件(包括勾选和点击加减按钮)
    handlecount(type, field) {
      if (type === "select") {
        this.count[field] = this.count[field] === 0 ? 1 : 0;
      } else if (type === "sub" && this.count[field] > 0) {
        this.count[field] = this.count[field] - 1;
      } else if (type === "add") {
        this.count[field] = this.count[field] + 1;
      }
    },
    //点击选择时间日期的事件
    showDatePicker(type) {
      if (type === "returnDate" && this.plan.period_type == 2) return false;
      this.dateType = type;
      if (!this.datePicker) {
        this.datePicker = this.$createDatePicker({
          title: "Date Picker",
          min: new Date(2008, 7, 8),
          max: new Date(2020, 9, 20),
          value: new Date(),
          cancelTxt: "Cancel",
          confirmTxt: "Confirm",
          onSelect: this.selectHandle,
          onCancel: this.cancelHandle
        });
      }
      this.datePicker.show();
      setTimeout(() => {
        this.datePicker.$updateProps({
          title: type === "departureDate" ? "Departure Date" : "Return Date",
          value:
            type === "departureDate"
              ? this.date.departureDateObj
              : this.date.returnDateObj
        });
      }, 0);
    },
    //点击时间日期插件确认键事件
    selectHandle(date, selectedVal, selectedText) {
      console.log(date, selectedVal, selectedText);
      this.date[this.dateType] = this.dateTozero(selectedVal).join("-");
      if (this.dateType === "departureDate" && this.plan.period_type == 2) {
        let dateArr = this.dateTozero(selectedVal);
        this.date.returnDate = [
          (Number(dateArr[0]) + 1).toString(),
          dateArr[1],
          dateArr[2] - 1
        ].join("-");
      }
      this.date[`${this.dateType}Obj`] = date;
    },
    //数字小于10时前面加个0
    dateTozero(arr) {
      return arr.map(item => (item < 10 ? "0" + item : "" + item));
    },
    //点击时间日期插件取消键事件
    cancelHandle() {},
    //点击Show My Results查看验证结果事件
    showResults() {
      if (!this.date.returnDate) {
        this.$createToast({
          txt: "请选择返程日期",
          time: 3000,
          type: "txt"
        }).show();
        return false;
      } else if (
        new Date(this.date.returnDateObj).getTime() <
        new Date(`${this.date.departureDate} 00:00:00`).getTime()
      ) {
        this.$createToast({
          txt: "返程日期不能小于去程日期",
          time: 3000,
          type: "txt"
        }).show();
        return false;
      }
      // else if ( this.plan.period_type == 1 && this.days > 180) {
      //   this.$createToast({
      //    txt: this.plan.plan_list_desc,
      //    time: 3000,
      //    type: 'txt'
      //   }).show();
      //   return false;
      // }
      else if (this.counts === 0) {
        this.$createToast({
          txt: "请选择行程人数",
          time: 3000,
          type: "txt"
        }).show();
        return false;
      } else if (this.counts >= 10) {
        this.$createToast({
          txt: "The maximum number of companion is 9",
          time: 3000,
          type: "txt"
        }).show();
        return false;
      } else if (this.count.myself == "0") {
        //如果没有myself就要显示确认框
        this.isShow = true;
        return false;
      }
      if (this.plan.period_type == 1) {
        this.checkDate();
      } else {
        this.next();
      }
    },
    //把数据存到vuex中的事件
    next() {
      const query = {
        count: this.count,
        days: this.days,
        date: this.date,
        product_id: this.product_id,
        phone_country_code: this.areacode
      };
      //将数据存到vuex
      this.setTravel(query);
      this.$router.push({ path: "/Infoconfirm" });
    },
    //自定义选择时间后做判断(年计划不需要)
    checkDate() {
      // 最多选择多少天
      var params = {
        product_id: this.plan.product_id,
        language: this.language,
        start_date: this.date.departureDate,
        end_date: this.date.returnDate
      };
      this.$http
        .post("/wesupromo/web/validate/checkJourneyDate", params)
        .then(res => {
          console.log(res);
          if (!res.data.is_valid) {
            this.$createToast({
              txt: res.msg,
              time: 3000,
              type: "txt"
            }).show();
          } else {
            this.next();
          }
        });
    },
    //隐藏支付方式
    Back() {
      this.isShow = false;
    },
    //继续支付操作
    Comfirm() {
      this.next();
    }
  },
  computed: {
    ...mapState(["areacode", "plan", "travelInfo", "product_id", "language"]),
    //计算总人数
    counts() {
      return (
        this.count.myself +
        this.count.spouse +
        this.count.child +
        this.count.friends
      );
    },
    //计算天数
    days() {
      let days = 0;
      if (
        this.date.returnDate &&
        new Date(this.date.returnDateObj).getTime() >=
          new Date(`${this.date.departureDate} 00:00:00`).getTime()
      ) {
        days =
          Math.ceil(
            (new Date(this.date.returnDateObj).getTime() -
              new Date(this.date.departureDateObj)) /
              (1000 * 60 * 60 * 24)
          ) + 1;
      }
      if (this.plan.period_type == 2) {
        days = 365;
      }
      return days;
    }
  }
};
</script>

<style lang="less" scoped>
@import "~style/mixin.less";
.page-wrap {
  background-color: #fff;
  .banner {
    display: block;
    width: 750px;
  }
  .container-wrap {
    padding: 24px 34px 260px 34px;
    position: relative;
    .title {
      color: #000;
      font-weight: 500;
      font-size: 44px;
      line-height: 52px;
      margin-bottom: 62px;
    }
    .part-title {
      color: #000;
      font-size: 28px;
      line-height: 34px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      > span {
        &:nth-of-type(1) {
          font-weight: 500;
        }
        &:nth-of-type(2) {
          color: rgb(155, 155, 155);
        }
      }
    }
    .date-part {
      overflow: hidden;
      margin-top: 18px;
      margin-bottom: 58px;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 22px;
      background: #fff;
      box-shadow: 0px 8px 40px 0 rgba(0, 0, 0, 0.14);
      /deep/ .cube-btn {
        background: #fff;
      }
      .date-pick {
        padding: 24px 0 20px 0;
        .date-label {
          color: @gray;
          font-weight: 500;
          margin: 0px 0 26px 0;
          font-size: 22px;
          line-height: 24px;
        }
        .date-value {
          color: @blue;
          font-weight: 500;
          font-size: 34px;
          line-height: 46px;
          &.empty {
            color: rgb(155, 155, 155);
          }
        }
      }
    }
    .Companion-part {
      padding: 15px 0;
      margin-top: 26px;
      margin-bottom: 84px;
      border-radius: 22px;
      background: #fff;
      box-shadow: 0px 8px 40px 0 rgba(0, 0, 0, 0.14);
      > div {
        padding: 0 38px 0 50px;
        margin: 23px 0;
        display: flex;
        justify-content: space-between;
        align-items: center;
        > span {
          vertical-align: middle;
          &.checkBox {
            display: inline-block;
            width: 44px;
            height: 44px;
            background: url("../assets/images/TickOff@3x.png") no-repeat center
              center/44px 44px;
            &.selected {
              background: url("../assets/images/Tick@3x.png") no-repeat center
                center/44px 44px;
            }
          }
          > span {
            vertical-align: middle;
            display: inline-block;
            width: 44px;
            height: 44px;
            text-align: center;
            line-height: 44px;
          }

          .action-button {
            vertical-align: middle;
            display: inline-block;
            width: 44px;
            height: 44px;
            &.sub {
              background: url("../assets/images/reduce@3x.png") no-repeat center
                center/44px 44px;
            }
            &.add {
              background: url("../assets/images/Add@3x.png") no-repeat center
                center/44px 44px;
            }
          }

          &:first-of-type {
            color: #000;
            font-weight: 500;
            font-size: 34px;
            line-height: 40px;
            > i {
              font-size: 20px;
              font-style: normal;
            }
          }
        }
      }
    }
    .button-bar {
      text-align: center;
      position: absolute;
      bottom: 0;
      left: 50%;
      width: 100%;
      height: 240px;
      background-color: rgba(250, 250, 250, 0.9);
      transform: translateX(-50%);
      margin-top: 54px;
      border-top:3px solid rgba(0, 0, 0, 0.25);
    }
    .next {
      width: 388px;
      height: 130px;
      line-height: 130px;
      border: none;
      border-radius: 10px;
      margin-top: 50px;
      background: rgb(2, 164, 71);
      color: #fff;
      text-align: center;
      font-size: 36px;
    }
  }
  //弹出框
  .popover_back {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1;
    right: 0;
    bottom: 0;
    background-color: #000;
    opacity: 0.4;
  }
  .popover {
    width: 600px;
    height: 600px;
    position: fixed;
    z-index: 999;
    left: 50%;
    top: 50%;
    transform: translate(calc(-50% - 2px), calc(-50% - 2px));
    background-color: #fff;
    border-radius: 25px;
    padding: 30px;
    .Oops {
      width: 150px;
      height: 150px;
      margin: 0 auto;
      img {
        display: block;
        width: 100%;
        height: 100%;
      }
    }
    .title {
      height: 80px;
      font-size: 40px;
      width: 100%;
      text-align: center;
      color: rgb(12, 129, 255);
    }
    .hint {
      text-align: center;
      color: rgb(3, 3, 3);
      line-height: 32px;
      font-size: 26px;
      height: 210px;
    }
    .btn {
      display: flex;
      .back {
        flex: 1;
        height: 100px;
        font-size: 33px;
        color: #fff;
        background-color: rgb(74, 74, 74);
        border-radius: 15px 0 0 15px;
      }
      .comfirm {
        flex: 1;
        height: 100px;
        margin-left: -0.5px;
        font-size: 33px;
        color: #fff;
        background-color: rgb(2, 164, 71);
        border-radius: 0 15px 15px 0;
      }
    }
  }
}
</style>


