<!--
 * @Description: In User Settings Edit
 * @Author: your name
 * @Date: 2019-08-29 10:37:02
 * @LastEditTime: 2019-09-09 12:59:23
 * @LastEditors: Please set LastEditors
 -->
<template>
  <div class="page-wrap">
    
     <div class="finish-item">
      <p class="done-img">
        <img src="@/assets/images/done@3x.png" alt />
      </p>
      <p>Purchase Complete</p>
    </div>

    <div class="email-item">
      <span class="email-img">
        <img src="@/assets/images/Email@3x.png" alt />
      </span>
      <span class="email-info">
        <p>Thank you! Insurer has already emailed you the policy!</p>
        <p>GoLion will send you the Acknowlegement email, we recommend you to download our App for more features!</p>
      </span>
    </div>

    <div class="finish-bottom">
     I acknowledge and agree that Insurer shall use the Personal data I have provided for the purposes of providing insurance services in accordance with Insurer's Privacy Policy. 
    </div> 

    <div class="button-bar">
      <button @click="toIndex" class="proceed">Finish</button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  created() {
    const toast = this.$createToast({
      time: 1000,
      txt: "loading..."
    });
    toast.show();
  },
  methods: {
    toIndex(){
      this.$router.push('/')
    }
  },
};
</script>

<style lang="less" scoped>
@import "~style/mixin.less";
.page-wrap {
  padding: 34px;
  background: @background;
   .imgs{
      width: 100%;
   }
   .img{
       width: 100%;
       height: 100%;
   }
  .finish-item {
    text-align: center;
    margin-top: 200px;
    .done-img {
      width: 170px;
      height: 170px;
      margin: 0 auto;
      img {
        width: 100%;
        height: 100%;
      }
    }
    p {
      color: @blue;
      font-size: 50px;
      margin-top: 30px;
    }
  }
  .email-item{
    display: flex;
    justify-content: space-around;
    margin-top: 200px;
    .email-img{
      width: 120px;
      height: 120px;
      margin-left: 40px;
      img{
        width: 100%;
        height: 100%;
      }
    }
    .email-info{
      flex: 1;
      margin-left: 30px;
      line-height: 25px;
      letter-spacing: 1px;
      p{
        color: rgb(0,129,255);
      }
    }
  }
  .finish-bottom{
    color: rgb(209, 207, 207);
    text-align: center;
    line-height: 30px;
    padding-top: 300px;
    padding-bottom: 70px;
  }
  .button-bar {
    margin: 0 auto;
    height: 240px;
    text-align: center;
    margin: 54px -34px -34px;
    background-color: rgba(0,0,0,0.1);
    display: flex;
    justify-content: center;
    align-items: center;
    border-top: 2px solid rgba(151,151,151,.5)
  }
  .proceed {
    width: 388px;
    height: 130px;
    line-height: 130px;
    border: none;
    border-radius: 10px;
    background: rgb(2, 164, 71);
    color: #fff;
    text-align: center;
    font-size: 36px;
  }
}
</style>


