<!--
 * @Description: In User Settings Edit
 * @Author: your name
 * @Date: 2019-08-29 10:34:42
 * @LastEditTime: 2019-09-17 15:16:19
 * @LastEditors: Please set LastEditors
 -->
<template>
  <div class="page-wrap">
    <div class="travelead">TraveLead(Essential)</div>
    <div class="date">
      <span>
        <p class="date-title">Departure Date</p>
        <p class="date-date">{{$store.state.travelInfo.date.departureDate}}</p>
      </span>
      <span>
        <p class="date-title">End Date</p>
        <p class="date-date">{{$store.state.travelInfo.date.returnDate}}</p>
      </span>
    </div>
    <!-- 显示客户姓名和成年还是child -->
    <div class="customer">
      <span class="userName">
        <p class="days">{{$store.state.travelInfo.days}}days</p>
        <!-- <p>andy</p>
        <p>Amy Kwok</p>
        <p>Adrian Cheng</p>
        <p>Wendy Lee</p>-->
        <!-- 如果保单人就要加一个保单人的而外信息 -->
        <p
          v-if="protectorShow"
        >{{$store.state.protectorInfo.givename}} {{$store.state.protectorInfo.surname}}</p>
        <p
          v-for="(item,index) in $store.state.userInfo"
          :key="index"
        >{{item.givename}} {{item.surname}}</p>
      </span>
      <span class="customer-type">
        <p style="color:transparent;">a</p>
        <!-- <p>(me)</p>
        <p>(adult)</p>0000
        <p>(child)</p>
        <p>(adult)</p>-->
        <p v-if="protectorShow">{{$store.state.protectorInfo.title}}</p>
        <p v-for="(item,index) in $store.state.userInfo" :key="index">{{identity(item.title)}}</p>
      </span>
    </div>

    <div class="subtotalPrice">
      <span>
        <p class="subTotal">SubTotal</p>
        <p>
          Levy
          <span class="collection">（Know more about IA Levy collection）</span>
        </p>
      </span>
      <span class="price">
        <p>HK${{price}}.00</p>
        <p>HK$0.11</p>
      </span>
    </div>

    <div class="total-item">
      <span class="total">Total</span>
      <span class="totalprice">HK${{price}}.11</span>
    </div>

    <!-- 单选 -->
    <div class="radio">
      <span @click="clickRadio" :class="{'checked':true, 'selected':checked}"></span>
      <p class="agreed">
        Please confirm you have read and agreed with the
        <span class="declaration">Declaration</span> and
        <span class="declaration">Personal Information Collection Statement.</span>
      </p>
    </div>

    <p @click="showPayMethod" class="payment-method">Payment Method</p>

    <div class="button-bar">
      <button @click="checkOut" class="proceed">Checkout</button>
    </div>
    <!-- 原生 -->
    <!-- 黑色背景框 -->
    <div v-show="isShow" class="popover_back"></div>
    <!-- 弹出框 -->
    <div v-show="isShow" class="popover">
      <!-- 标题 -->
      <p class="choose_card">Choose credit card</p>
      <!-- 支付银行卡图片部分 -->
      <div class="pay_img">
        <cube-radio-group class="pay_radio">
          <cube-radio
            class="input_radio"
            v-for="(option, index) in options"
            :key="index"
            :option="option"
            @input="input1"
            v-model="selected"
          >
            <img class="bank_card" :src="option.src" />
          </cube-radio>
        </cube-radio-group>
      </div>
      <!-- 输入框部分 -->
      <div class="pay_inform">
        <div class="cardHolderName">
          <p class="cardName">Card holder name</p>
          <input v-model="userCardInfo.name" class="car_input" type="text" />
          <p class="cardName">Card number</p>
          <input v-model="userCardInfo.number" class="car_input" type="text" />
        </div>
        <div class="cardNumber">
          <div class="left">
            <div class="Expiration">
              <p class="cardName">Expiration</p>
              <input v-model="userCardInfo.expiration" class="car_input" type="text" />
            </div>
            <div class="data">
              <p class="cardName">data</p>
              <input v-model="userCardInfo.data" class="car_input" type="text" />
            </div>
          </div>
          <div class="right">
            <p class="cardName">CVV</p>
            <input v-model="userCardInfo.CVV" class="car_input" type="text" />
          </div>
        </div>
      </div>
      <!-- 下划线 -->
      <div class="line"></div>
      <div class="payButton">
        <button @click="clickCancel" class="buttonCancel">取消</button>
        <button @click="clickConfirm" class="buttonConfirm">确定</button>
      </div>
    </div>
  </div>
</template>
<script>
//导入vuex
import { mapState, mapMutations } from "vuex";
//导入图片
import img1 from "../assets/images/mastercard@3x.png";
import img2 from "../assets/images/visa@3x.png";
import img3 from "../assets/images/150px-American_Express_logo_(2018).svg@3x.png";
import img4 from "../assets/images/JCB_logo.svg@3x.png";
import img5 from "../assets/images/download@3x.png";
export default {
  data() {
    return {
      count: {
        confirm: 0
      },
      //价格
      price: "",
      //控制阅读是否勾选
      checked: false,
      //控制支付框的显示
      isShow: false,
      //控制保单人信息的显示
      protectorShow: false,
      //支付方式
      selected: "",
      options: [
        {
          label: "1",
          value: "1",
          src: img1
        },
        {
          label: "2",
          value: "2",
          src: img2
        },
        {
          label: "3",
          value: "3",
          src: img3
        },
        {
          label: "4",
          value: "4",
          src: img4
        },
        {
          label: "5",
          value: "5",
          src: img5
        }
      ],
      //保存银行卡信息
      userCardInfo: {
        name: "",
        number: "",
        expiration: "",
        data: "",
        CVV: ""
      }
    };
  },
  created() {
    const toast = this.$createToast({
      time: 1000,
      txt: "loading..."
    });
    toast.show();
    console.log(this.$store.state);
    if (this.$store.state.protectorInfo) {
      this.protectorShow = true;
    }
  },
  methods: {
    clickRadio() {
      this.checked = !this.checked;
    },
    //显示支付方式
    showPayMethod() {
      this.isShow = true;
    },
    //检验银行卡信息是否存在
    hasCardInfo() {
      var hasInfo = true;
      console.log(this.selected);
      console.log(this.userCardInfo.name.trim());
      if (
        this.userCardInfo.name.trim() == "" ||
        this.userCardInfo.number.trim() == "" ||
        this.userCardInfo.expiration.trim() == "" ||
        this.userCardInfo.data.trim() == "" ||
        this.userCardInfo.CVV.trim() == "" 
      ) {
        console.log('kong');
        hasInfo = false;
      }
      if (this.selected == null || this.selected == '') {
        console.log('空');
        hasInfo = false;
      }
      return hasInfo;
    },
    //点击checkout按钮事件
    checkOut() {
      var hasInfo = this.hasCardInfo();
      console.log(hasInfo);
      //判断是否勾选了
      if (this.checked != true) {
        this.$createToast({
          txt: "您有选项未勾选",
          time: 3000,
          type: "txt"
        }).show();
        return false;
      } else if (!hasInfo) {
        this.$createToast({
          txt: "请填写完全支付信息",
          time: 3000,
          type: "txt"
        }).show();
        return false;
      } else {
        console.log('请调用接口');
        
        // var params = {
        //   application_id: "6d239064ae334954",
        //   others_insured_list: [
        //     {
        //       doc_firstname: "xiaoyu",
        //       doc_surname: "Cat",
        //       doc_type: "idcard",
        //       doc_id: "c668668",
        //       dob: "1980-05-20",
        //       relation: 2001,
        //       title: "Mr."
        //     },
        //     {
        //       doc_firstname: "sam",
        //       doc_surname: "son",
        //       doc_type: "passport",
        //       doc_id: "123456",
        //       dob: "1980-05-20",
        //       relation: 2003,
        //       title: "Mr."
        //     }
        //   ],
        //   ccinfo: {
        //     card_no: "5422882800700007",
        //     expired_month: "07",
        //     expired_year: "20",
        //     card_holder_name: "testing card",
        //     card_scheme: "Master",
        //     cvc: "123"
        //   },
        //   id: "5aaf191c9a8cc93854f23efd",
        //   insured_mem_id: "5aaf191c9a8cc93854f23efd",
        //   payment_type: 12,
        //   product_id: "5d15cda9971f320d262b810a",
        //   start_date: "2019-09-11",
        //   end_date: "2019-09-20",
        //   operation: "travel"
        // };
        // this.$http
        //   .post("/api/web/checkout/purchasebystarr", params)
        //   .then(res => {
        //     console.log(res);
        //   });
        //this.$router.push("/Complete");
      }
    },
    //cube-ui的radio组件的点击事件,获取到选中的第几个
    input1(value) {
      console.log(value);
      this.selected = value;
    },
    //点击支付取消按钮
    clickCancel() {
      this.$createDialog({
        type: "confirm",
        zIndex: 999,
        icon: "cubeic-alert",
        //title: '我是标题',
        content: "是否退出?",
        confirmBtn: {
          text: "确定",
          active: true,
          disabled: false,
          href: "javascript:;"
        },
        cancelBtn: {
          text: "取消",
          active: false,
          disabled: false,
          href: "javascript:;"
        },
        //点击了取消按钮
        onConfirm: () => {
          this.$createToast({
            type: "warn",
            zIndex: 999,
            time: 2000,
            txt: "已取消"
          }).show();
          this.isShow = false;
          // this.userCardInfo = {
          //   name: "",
          //   number: "",
          //   expiration: "",
          //   data: "",
          //   CVV: ""
          // };
          // this.selected = "";
          console.log(this.userCardInfo);
        }
      }).show();
    },
    //点击支付确认按钮
    clickConfirm() {
      //判断用户输入的信息正确
      if (
        this.userCardInfo.name == "" ||
        this.userCardInfo.number == "" ||
        this.userCardInfo.expiration == "" ||
        this.userCardInfo.data == "" ||
        this.userCardInfo.CVV == ""
      ) {
        this.$createToast({
          txt: "填写信息不能为空",
          time: 3500,
          zIndex: 999,
          type: "txt"
        }).show();
        return false;
      }
      if (this.selected == "") {
        this.$createToast({
          txt: "请选择支付方式",
          time: 3500,
          zIndex: 999,
          type: "txt"
        }).show();
        return false;
      }

      console.log(this.userCardInfo);
      this.isShow = false;
    }
  },
  //使用计算属性实现成员是adult还是child
  computed: {
    ...mapState(["travelInfo", "product_id", "language", "plan"]),
    identity() {
      return function(value) {
        switch (value) {
          case "Myself":
            return "me";
            break;
          case "My Spouse":
            return "adult";
            break;
          case "My Child":
            return "child";
            break;
          case "Family and friends":
            return "adult";
            break;
        }
      };
    }
  },
  mounted() {
    //验证HKID
    // var params = {
    //   language: "tc",
    //   hkid: "P1032651"
    // };
    // this.$http
    //   .post("/wesupromo/web/validate/validateHKIDWithLng", params)
    //   .then(res => {
    //     console.log(res);
    //   });
    //获取价格
    // 报价
    let others_insured_list = [];
    const { count } = this.travelInfo;
    if (count.myself) {
      others_insured_list.push({ relation: "2001" });
    }
    if (count.spouse) {
      others_insured_list.push({ relation: "2002" });
    }
    if (count.child) {
      others_insured_list.push({ relation: "2003" });
    }
    if (count.friends) {
      others_insured_list.push({ relation: "2004" });
    }

    var priceParams = {
      product_id: this.plan.product_id,
      //"03dfd96d875d43ba8ced522f3bbc0179"
      language: this.language,
      start_date: this.travelInfo.date.departureDate,
      end_date: this.travelInfo.date.returnDate,
      others_insured_list
    };
    // 日计划  月计划   年计划
    console.log(this.travelInfo.date.departureDate);
    console.log(this.travelInfo.date.returnDate);

    //调用接口获取价格
    this.$http
      .post("/wesupromo/web/product/getstarrquote", priceParams)
      .then(res => {
        console.log(res);
        this.price = res.data.premium;
      });
  }
};
</script>

<style lang="less" scoped>
@import "~style/mixin.less";
* {
  touch-action: pan-y;
}
.page-wrap {
  padding: 34px;
  background: @background;
  .imgs {
    width: 100%;
  }
  .img {
    width: 100%;
    height: 100%;
  }

  .travelead {
    text-align: center;
    color: @blue;
    font-size: 35px;
  }
  .date {
    display: flex;
    justify-content: space-between;
    border-top: 1px solid rgb(209, 208, 208);
    border-bottom: 1px solid rgb(209, 208, 208);
    line-height: 60px;
    padding-left: 100px;
    padding-right: 100px;
    margin-top: 50px;
    span {
      .date-title {
        color: rgb(74, 74, 74);
      }
      .date-date {
        font-size: 30px;
        color: rgb(155, 155, 155);
      }
    }
  }
  .customer {
    display: flex;
    justify-content: space-between;
    line-height: 50px;
    padding-left: 100px;
    padding-right: 100px;
    text-align: center;
    font-size: 26px;
    .userName {
      p {
        color: rgb(74, 74, 74);
      }
    }
    .customer-type {
      color: rgb(155, 155, 155);
    }
  }
  .subtotalPrice {
    display: flex;
    justify-content: space-between;
    border-top: 1px solid #9fbcd1;
    border-bottom: 1px solid #9fbcd1;
    line-height: 50px;
    padding-top: 50px;
    padding-bottom: 15px;
    span {
      .subTotal {
        color: rgb(74, 74, 74);
        font-size: 29px;
        font-family: HelveticaNeue-Medium;
        letter-spacing: 0.05px;
      }
      p {
        .collection {
          color: @blue;
        }
      }
    }
    .price {
      text-align: right;
      p {
        color: rgb(24, 95, 146);
        font-size: 29px;
      }
    }
  }
  .total-item {
    display: flex;
    justify-content: space-between;
    padding-top: 30px;
    padding-bottom: 30px;

    .total {
      color: rgb(74, 74, 74);
      font-size: 29px;
      font-family: HelveticaNeue-Medium;
      letter-spacing: 0.05px;
    }
    .totalprice {
      color: rgb(24, 95, 146);
      font-size: 29px;
    }
  }

  .radio {
    display: flex;
    border-bottom: 1px solid #9fbcd1;
    padding-bottom: 250px;
    span {
      &.checked {
        display: inline-block;
        width: 50px;
        box-sizing: border-box;
        height: 50px;
        border: 1px solid #888;
        background: none;
        border-radius: 50%;
        background: url("../assets/images/TickOff@3x.png") no-repeat center
          center/44px 44px;
      }
      &.selected {
        border: none;
        border-radius: 0;
        background: url("../assets/images/Tick@3x.png") no-repeat center
          center/50px 50px;
      }
    }
    .agreed {
      flex: 1;
      margin-left: 40px;
      color: @gray;
      line-height: 28px;
      font-size: 20px;
      // text-align: justify;
      .declaration {
        text-decoration: underline;
        color: @blue;
      }
    }
  }
  .payment-method {
    font-size: 30px;
    color: rgb(74, 74, 74);
    font-family: HelveticaNeue-Medium;
    margin-top: 25px;
  }
  .button-bar {
    margin: 0 auto;
    text-align: center;
    margin-top: 54px;
    margin-bottom: 102px;
  }
  .proceed {
    width: 388px;
    height: 130px;
    line-height: 130px;
    border: none;
    border-radius: 10px;
    background: rgb(2, 164, 71);
    color: #fff;
    text-align: center;
    font-size: 36px;
  }
}
//弹出框
.popover_back {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
  right: 0;
  bottom: 0;
  background-color: #000;
  opacity: 0.4;
}
.popover {
  width: 630px;
  height: 830px;
  position: fixed;
  z-index: 999;
  left: 50%;
  top: 50%;
  transform: translate(calc(-50% - 1px), calc(-50% - 1px));
  background-color: #fff;
  border-radius: 25px;
  padding: 30px;
  .choose_card {
    width: 100%;
    height: 35px;
    font-size: 30px;
    line-height: 35px;
    color: rgb(74, 74, 74);
    font-weight: bold;
  }
  .pay_img {
    width: 100%;
    padding: 5px;
    overflow: hidden;
    margin-bottom: 10px;
    .pay_radio {
      width: 100%;
      margin-top: 20px;
      .input_radio {
        float: left;
        width: 33.33%;
        padding: 0;
      }
      .bank_card {
        width: 100px;
        height: 100px;
        margin-left: 15px;
      }
    }
  }
  .pay_inform {
    padding: 0 15px;
    .cardHolderName {
      .cardName {
        height: 20px;
        width: 100%;
        margin: 10px 0 8px 0;
        line-height: 20px;
      }
      .car_input {
        height: 60px;
        width: 100%;
        border: 2px solid rgba(151, 151, 151, 1);
        margin: 0;
      }
    }
    .cardNumber {
      .left {
        float: left;
        .Expiration {
          display: inline-block;
          .cardName {
            height: 20px;
            width: 100%;
            margin: 10px 0 8px 0;
            line-height: 20px;
          }
          .car_input {
            height: 60px;
            width: 100px;
            border: 2px solid rgba(151, 151, 151, 1);
            margin: 0;
          }
        }
        .data {
          display: inline-block;
          .cardName {
            height: 20px;
            width: 100%;
            margin: 10px 0 8px 0;
            line-height: 20px;
          }
          .car_input {
            height: 60px;
            width: 100px;
            border: 2px solid rgba(151, 151, 151, 1);
            margin: 0;
          }
        }
      }
      .right {
        float: right;
        .cardName {
          height: 20px;
          width: 100%;
          margin: 10px 0 8px 0;
          line-height: 20px;
        }
        .car_input {
          height: 60px;
          width: 200px;
          border: 2px solid rgba(151, 151, 151, 1);
          margin: 0;
        }
      }
    }
  }
  .line {
    height: 3px;
    width: 100%;
    background-color: rgba(151, 151, 151, 1);
    position: absolute;
    bottom: 150px;
    left: 0;
  }
  .payButton {
    position: absolute;
    bottom: 33px;
    left: 0;
    width: 100%;
    height: 80px;
    .buttonCancel {
      display: inline-block;
      width: 40%;
      height: 80px;
      border: 0;
      font-size: 33px;
      color: #fff;
      background-color: rgb(74, 74, 74);
      border-radius: 15px 15px;
      margin-left: 50px;
    }
    .buttonConfirm {
      display: inline-block;
      width: 40%;
      border: 0;
      height: 80px;
      font-size: 33px;
      color: #fff;
      margin-left: 30px;
      background-color: #02a447;
      border-radius: 15px 15px;
    }
  }
}
</style>


