<!--
 * @Description: In User Settings Edit
 * @Author: your name
 * @Date: 2019-09-03 19:35:18
 * @LastEditTime: 2019-09-16 15:51:12
 * @LastEditors: Please set LastEditors
 -->
<template>
  <!-- Myself -->
  <div class="contain">
    <div class="hint">Policyholder must be 18 or above</div>
    <div class="form-wrap">
      <div class="title">Policyholder</div>
      <div ref="inputInfo">
        <!-- 用户信息输入框 -->
        <div class="form-item">
          <p>Gender*</p>
          <!-- @blur="genderBlur" -->
          <input class="Gender" v-model="form.gender" type="text" placeholder="Gender" />
        </div>
        <div class="form-item">
          <p>Surname*</p>
          <!-- @blur="SurnameBlur" -->
          <input class="Gender" v-model="form.surname" type="text" placeholder="Surname" />
        </div>

        <div class="form-item">
          <p>Given Name*</p>
          <!-- @blur="givenameBlur" -->
          <input class="Gender" v-model="form.givename" type="text" placeholder="Given Name" />
        </div>
        <!-- 日期插件 -->
        <cube-button>
          <div class="form-item">
            <p>Date of birth*</p>
            <!-- @blur="birthDateBlur" -->
            <input
              ref="inputBirth"
              class="Gender"
              @focus="showDatePicker"
              v-model="form.datebirth"
              type="text"
              placeholder="Date of birth"
            />
          </div>
        </cube-button>

        <!-- 选择HKID按钮和passport Number按钮 -->
        <div>
          <button class="HKID" @click="inputHkid" :class="[select==1?'blue':'']">
            HKID
            <i :class="[select==1?'cubeic-ok':'']"></i>
          </button>
          <button class="passport" @click="inputPassport" :class="[select==2?'blue':'']">
            Passport Number
            <i :class="[select==2?'cubeic-ok':'']"></i>
          </button>
        </div>
        <!-- 输入框 -->
        <div class="HK">
          <span class="hkNumber">{{select==1?'HKID Number :':'Passport Number :'}}</span>
          <div class="right" v-show="select == 1">
            <span>
              <!-- @blur="HkidBlur" -->
              <input
                v-model="form.HKID1"
                class="IDnumber"
                ref="HKID1"
                type="text"
                placeholder="A123456"
              />
            </span>
            <span v-show="select == 1">(</span>
            <span v-show="select == 1">
              <input ref="HKID2" v-model="form.HKID2" class="num" type="text" placeholder="7" />
            </span>
            <span v-show="select == 1">)</span>
          </div>
          <div class="right" v-show="select == 2">
            <span>
              <!--  @blur="passportBlur" -->
              <input
                ref="HKID_passport"
                v-model="form.HKID_passport"
                :class="[select==2?'width':'']"
                class="IDnumber"
                type="text"
                placeholder="A123456"
              />
            </span>
          </div>
        </div>
      </div>
    </div>
    <div class="button-bar">
      <button @click="confirm" class="proceed">Confirm</button>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  name: "formItem",

  data() {
    return {
      form: {
        gender: "",
        surname: "",
        givename: "",
        datebirth: "",
        HKID_passport: "",
        HKID1: "",
        HKID2: "",
        title: "Policyholder"
      },
      //判断点击之后的按钮
      select: 1
    };
  },
  created() {
    console.log(this.form);
    this.getProtectorInfo();
    console.log(this.form);
  },

  methods: {
    ...mapMutations(["setProtectorInfo"]),
    inputHkid() {
      //如果有值就把select改为2
      if (this.$refs.HKID_passport.value.trim() != "") {
        this.$createToast({
          txt: "passport Number有值了!",
          time: 3000,
          type: "txt"
        }).show();
        this.select = 2;
      } else {
        this.select = 1;
      }
    },
    inputPassport() {
      if (
        this.$refs.HKID1.value.trim() != "" ||
        this.$refs.HKID2.value.trim() != ""
      ) {
        this.$createToast({
          txt: "HKID Number有值了!",
          time: 3000,
          type: "txt"
        }).show();
        this.select = 1;
      } else {
        this.select = 2;
      }
    },
    //确认事件
    confirm() {
      console.log(this.form);
      //验证信息是否都填写完毕
      if (
        this.form.datebirth == "" ||
        this.form.givename == "" ||
        this.form.gender == "" ||
        this.form.surname == ""
      ) {
        this.$createToast({
          txt: "有信息未填写",
          time: 3000,
          type: "txt"
        }).show();
        return false;
      }
      if (this.form.HKID1 == "" && this.form.HKID_passport == "") {
        this.$createToast({
          txt: "有信息未填写",
          time: 3000,
          type: "txt"
        }).show();
        return false;
      }
      //调用验证方法,验证信息填写是否符合规定
      var isverify1 = this.verifyUserFrom(this.form);
      if (isverify1 == false) {
        return false;
      }

      //如果验证都通过
      console.log(isverify1);
      if (isverify1 == true) {
        this.form.gender = this.form.gender || "";
        this.form.surname = this.form.surname || "";
        this.form.givename = this.form.givename || "";
        this.form.datebirth = this.form.datebirth || "";
        this.form.HKID1 = this.form.HKID1 || "";
        this.form.HKID2 = this.form.HKID2 || "";
        this.form.HKID_passport = this.form.HKID_passport || "";
        this.form.title = "Policyholder";
        //把数据存到vuex中
        this.setProtectorInfo(this.form);
        this.$router.push({ path: "/confirmOrder" });
      }
    },
    //如果vuex中有保单人信息,获取渲染到页面
    getProtectorInfo() {
      if (this.$store.state.protectorInfo) {
        console.log("当protectorInfo有值时,把值赋值给form");
        this.form = {};
        for (var key in this.$store.state.protectorInfo) {
          this.form[key] = this.$store.state.protectorInfo[key];
        }
        //判断再次进入信息编辑页的时候passport是否有值
        if (this.form.HKID_passport != "") {
          this.select = 2;
        }
      }
    },
    //封装判断用户输入的信息是否符合规定
    verifyUserFrom(protectorInfo) {
      var isverify = false;
      //验证用户性别
      if (
        protectorInfo.gender != "male" &&
        protectorInfo.gender != "female" &&
        protectorInfo.gender != "男" &&
        protectorInfo.gender != "女"
      ) {
        this.$createToast({
          txt: "请输入正确的性别( male / female / 男 / 女)",
          time: 3500,
          type: "txt"
        }).show();
        return false;
      }
      //验证用户surname
      var surnameinput = new RegExp("^[a-zA-Z]+$");
      if (!surnameinput.test(protectorInfo.surname)) {
        this.$createToast({
          txt: "Surname输入有误,只能输入全英文且不能为空",
          time: 3500,
          type: "txt"
        }).show();
        return false;
      }
      //验证用户given name
      var surnameinput1 = new RegExp("^[a-zA-Z]+$");
      if (!surnameinput1.test(protectorInfo.givename)) {
        this.$createToast({
          txt: "Surname输入有误,只能输入全英文且不能为空",
          time: 3500,
          type: "txt"
        }).show();
        return false;
      }
      //验证passport
      var passportInput = new RegExp("^[0-9]*$");
      if (
        protectorInfo.HKID_passport.trim() != "" &&
        !passportInput.test(protectorInfo.HKID_passport)
      ) {
        this.$createToast({
          txt: "passportInput输入有误请重新输入",
          time: 3500,
          type: "txt"
        }).show();
        return false;
      }
      //验证HKID(调用接口)
      var HKIDInput = /^[a-z0-9]{8}$/;
      if (
        protectorInfo.HKID1.trim() != "" &&
        !HKIDInput.test(protectorInfo.HKID1)
      ) {
        this.$createToast({
          txt: "HKID输入有误请重新输入,只能输入8位数",
          time: 3500,
          type: "txt"
        }).show();
        return false;
      }
      isverify = true;
      console.log(isverify);

      return isverify;
    },
    //点击日期插件
    showDatePicker() {
      if (!this.datePicker) {
        this.datePicker = this.$createDatePicker({
          title: "Date of birth",
          min: new Date(1980, 7, 28),
          max: new Date(),
          value: new Date(),
          cancelTxt: "Cancel",
          confirmTxt: "Confirm",
          onSelect: this.selectHandle,
          onCancel: this.cancelHandle
        });
      }
      this.datePicker.show();
    },
    //点击确认事件
    selectHandle(date, selectedVal, selectedText) {
      this.form.datebirth = this.dateTozero(selectedVal).join("-");
      console.log(this.form.datebirth);
      //因为cube-ui再次选完日期不会修改日期的显示
      this.$refs.inputBirth.value = this.form.datebirth;
    },
    //点击取消
    cancelHandle() {},
    //数字小于10时前面加个0
    dateTozero(arr) {
      return arr.map(item => (item < 10 ? "0" + item : "" + item));
    }
  },
  computed: {}
};
</script>

<style lang="less" scoped>
@import "~style/mixin.less";
.contain {
  padding: 30px;
  background-color: #fff;
  height: 100%;
  .hint {
    color: rgb(88, 89, 95);
    margin-bottom: 30px;
    margin-top: 20px;
  }
  .form-wrap {
    padding: 20px;
    box-shadow: 0px 5px 10px 0 rgba(0, 0, 0, 0.14);
    // line-height: 65px;
    margin-bottom: 30px;
    background-color: #fff;
    .title {
      font-size: 32px;
      color: @blue;
      font-weight: bold;
      margin-top: 20px;
      margin-bottom: 50px;
    }
    p {
      display: flex;
      justify-content: space-between;
      .member {
        color: @blue;
        font-weight: bold;
        font-size: 32px;
      }
      .member-num {
        color: @blue;
        font-weight: bold;
        font-size: 32px;
        margin-left: 15px;
      }
      .member-title {
        font-weight: bold;
        font-size: 32px;
        color: rgb(0, 0, 0);
        font-family: HelveticaNeue-Medium;
      }
    }
    .Gender {
      height: 75px;
      width: 100%;
      border-radius: 15px;
      font-size: 30px;
      border: 2px solid rgba(173, 175, 175, 0.199);
      text-indent: 20px;
      font-weight: bold;
    }
    input::-webkit-input-placeholder {
      color: rgb(233, 233, 233);
      letter-spacing: 2px;
      font-weight: 100;
    }
    .form-item {
      p {
        color: rgb(0, 0, 0);
      }
      line-height: 35px;
      margin-top: 20px;
    }
    .HKID {
      width: 316px;
      height: 56px;
      color: white;
      font-size: 30px;
      outline: none;
      border: none;
      background: rgb(236, 235, 235);
      border-radius: 10px;
      margin-top: 40px;
    }
    .passport {
      width: 316px;
      height: 56px;
      padding: 0;
      text-align: center;
      color: white;
      font-size: 30px;
      background: rgb(236, 235, 235);
      outline: none;
      border: none;
      border-radius: 10px;
      margin-top: 40px;
    }
    .blue {
      background: @blue;
    }
    .HK {
      margin-top: 30px;
      font-size: 32px;
      margin-bottom: 10px;
      .left {
        display: inline-block;
      }
      .right {
        display: inline-block;
      }
      .hkNumber {
        color: rgb(0, 0, 0);
        // font-weight: bold;
        font-family: HelveticaNeue;
      }
      span {
        .IDnumber {
          width: 235px;
          height: 60px;
          border-radius: 13px;
          font-size: 30px;
          padding-right: 15px;
          border: 2px solid rgba(173, 175, 175, 0.199);
          margin-left: 20px;
          text-align: right;
        }
        .num {
          width: 75px;
          height: 60px;
          border-radius: 13px;
          font-size: 30px;
          border: 2px solid rgba(173, 175, 175, 0.199);
          text-align: center;
        }
        .width {
          width: 300px;
        }
      }
    }
  }
  .button-bar {
    margin: 0 auto;
    text-align: center;
    margin-top: 54px;
    margin-bottom: 102px;
  }
  .proceed {
    width: 388px;
    height: 130px;
    line-height: 130px;
    border: none;
    border-radius: 10px;
    background: rgb(2, 164, 71);
    color: #fff;
    text-align: center;
    font-size: 36px;
  }
  .cube-btn {
    padding: 0;
    background-color: #fff;
  }
}
</style>


