 * @LastEditTime: 2019-09-17 13:47:50
 * @LastEditors: Please set LastEditors
 -->
<template>
  <div class="page-wrap">
    <!-- <div class="">Coverage Details</div> -->
    <div class="contain">
      <div class="pageNum" v-for="(item,index) in datalist" :key="index">
        <p class="title">{{index +1 }}.{{item.name}}</p>
        <p class="details-item">{{item.details}}</p>
        <p class="details" v-for="(items,i) in item.tnclist" :key="i">
          <span class="desc">{{items.desc}}</span>
          <span>{{items.insured_sum}}</span>
        </p>
      </div>
    </div>
    <div class="button-bar">
      <button class="proceed" @click="proceed">Proceed</button>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";

export default {
  data() {
    return {
      datalist: ""
    };
  },
  computed: {
    ...mapState(["product_id", "language", "plan"])
  },
  mounted() {
    // var productId = this.product_id;
    // console.log(productId);

    const params = {
      product_id: this.product_id,
      language: this.language
    };

    this.$http.post("/wesupromo/web/tpc/getProductTerms", params).then(res => {
      console.log(res);
      this.datalist = res.data.product_terms_list;
      console.log(this.datalist[0].tnclist);

      //
    });
  },
  methods: {
    proceed() {
      this.$router.go(-1);
    }
  }
};
</script>
<style lang="less" scoped>
@import "~style/mixin.less";
.page-wrap {
  padding: 50px;
  padding-bottom: 260px;
  position: relative;
  height: 100%;
  .contain {
    height: 1023px;
    overflow: auto;
    
    .pageNum {
      margin-top: 60px;
      .title {
        color: @blue;
        font-size: 35px;
        border-bottom: 1px solid rgb(209, 208, 208);
        padding-bottom: 20px;
      }
      .details-item {
        border-bottom: 1px solid rgb(209, 208, 208);
        padding-bottom: 20px;
        padding-top: 25px;
      }
      .details {
        display: flex;
        justify-content: space-between;
        border-bottom: 1px solid rgb(209, 208, 208);
        padding-bottom: 15px;
        padding-top: 25px;
        span {
          line-height: 35px;
        }
        .desc {
          display: inline-block;
          width: 70%;
        }
      }
    }
  }
  // 溢出滚动并取消滚动条
  .contain::-webkit-scrollbar {display:none};

  .button-bar {
    text-align: center;
    position: absolute;
    bottom: 0;
    left: 50%;
    width: 100%;
    height: 240px;
    border-top: 3px solid rgba(0, 0, 0, 0.25);
    background-color: rgba(250, 250, 250, 0.9);
    transform: translateX(-50%);
  }

  .proceed {
    width: 388px;
    height: 130px;
    line-height: 130px;
    border: none;
    border-radius: 10px;
    margin-top: 50px;
    background: rgb(2, 164, 71);
    color: #fff;
    text-align: center;
    font-size: 36px;
  }
}
</style>


